
html,
.container,
.imgbox {
  width: 100%;
}
body {
  margin: 0;
}
.imgbox {
  position: relative;
  padding-bottom: 1rem;
}
.imgbox img {
  width: 100%;
}
.btn {
  position: fixed;
  width: 5rem;
  bottom: 0.4rem;
  left: 50%;
  margin-left: -2.5rem;
  text-align: center;
}
.btn > img {
  width: 100%;
}